import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo from '../../static/logo/dumna.svg'

const StyledLink = styled(Link)`
    position: fixed;
    display: inline-block;
    top: -15px;
    width: 355px;
    transition: all 0.25s ease-out
`

const Nav = styled.nav`
    position: relative;
    transform: translateY(45%);
    display: flex;
    justify-content: center;
    font-weight: 500;
    font-size: 1rem;
    align-items: center;
    height: 50px;
    top: 268px;
    align-items: center;
    background: #A92228;
    width: 100%;
    a {
        font-family: "Domine";
        color: #f0f0f0;
        padding-left: 2rem;
        transition: all ${props => props.theme.transitions};
        overflow: hidden;
        &:hover>div {
            width: 100%;
            left: 0%;
            background-color: #f0f0f0;
            transition: all 0.35s ease-out;
        }
        div {
            position: relative;
            height: 2px;
            width: 0%;
            top: 1px;
            left: 50%;
            background-color: #ded3d3;
            transition: all 0.35s ease-out;
        }
    }
`

const Pinned = function() {
    document.getElementById("main-header-logo").style.opacity = "0";
    document.getElementById("nav").style.transform = "translateY(45%)";
    document.getElementById("main_spacer").style.height= "125px";
    const mq = window.matchMedia( "(min-width: 1000px)" );
    if (mq.matches) {
        document.getElementById("main_spacer").style.height= "125px";
        console.log("300px")
        } else {
        document.getElementById("main_spacer").style.height= "25px";
        console.log("150px")   
    }
    //let postWrapper = document.getElementsByClassName("post_wrapper");
    // console.log(postWrapper)
    // for(var i = 0; i < postWrapper.length; i++){
    //     postWrapper[i].style.transform = "translateY(-45%)";
    // }
};

const unPinned = function() {
    document.getElementById("main-header-logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(0%)";
};
const unFixed = function() {
    document.getElementById("main-header-logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(45%)";
    const mq = window.matchMedia( "(min-width: 1000px)" );
    if (mq.matches) {
        document.getElementById("main_spacer").style.height= "300px";
        console.log("300px")
        } else {
        document.getElementById("main_spacer").style.height= "25px";
        console.log("150px")   
    }
    // let postWrapper = document.getElementsByClassName("post_wrapper");
    // for(var i = 0; i < postWrapper.length; i++){
    //     postWrapper[i].style.transform = "translateY(0%)";
    // }
};

const NavBar = () => (
    <Headroom id="main_headroom" disableInlineStyles hide pin
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={50} pinAnchor={0} hideAnchor={1000} >
        <StyledLink id="main-header-logo" to="/">
            <img src={logo} alt='Dumna Niepodległa Logo' />
        </StyledLink>
        <Nav id="nav">
            <Link to='/'>
                Wydarzenia
                <div />
            </Link>
            <Link to='/'>
                O nas
                <div />
            </Link>
            <Link to='/'>
                Kontakt
                <div />
            </Link>
            <Link to='/'>
                Sklep
                <div />
            </Link>
        </Nav>
    </Headroom>
);

export default NavBar